<template>
    <label class="lqb-form-compontent lqb-invitecode">
        <span class="lqb-comtitle">邀请人：</span> 
        <lqbInput  :style="'width:'+space"  v-model="inputValue.InviteCode" placeholder="请输入邀请码" ></lqbInput>
        <lqbButton :disabled="inputValue.InviteCodeState" type="primary" @click="checkCode"  >{{inputValue.InviteCodeState?"已验证":"验证邀请码"}}</lqbButton>
    </label>
</template>

<script>
import {Input,Button} from "element-ui";
import ajax from "ajax";
export default {
    data(){
        return{
            inputValue:this.value
        }
    },
    props:["value","size"],
    mounted(){
        console.log(this);
    },
    components:{
        "lqbInput":Input,
        "lqbButton":Button
    },
    computed:{
        space(){
            return (this.size=="long")?"480px":"165px"
        },
        inputValue:{
            get:function() {
                return this.value;
            },
            set:function(val) {
                this.$emit('input', val);
            }
        }
    },
    methods:{
        checkCode(){
            console.log(1111)
            this.inputValue.InviteCodeState=true;
            return;
            ajax.get(
                "test.json",
                {},
                function(res){
                    if(res.code==200){
                        inputValue.InviteCodeState=true;
                    }else{
                        inputValue.InviteCodeState=false;
                        alert("邀请码有误");
                    }
                }
            );
        }
    }
}
</script>
<style>
</style>



