<style scoped>

.lqbui-side {
    position: fixed;
    top: 50px;
    bottom: 0;
    z-index: 99;
    width: 160px;
    overflow-x: hidden;
}
.lqbui-bg-black {
    background-color: #393D49!important;
}
.lqbui-side-scroll {
    width: 180px;
    height: 100%;
    overflow-x: hidden;
}
.lqbui-nav-tree {
    position: relative;
    background-color: #393D49;
    color: #fff;
    border-radius: 2px;
    font-size: 0;
    box-sizing: border-box;
    width: 160px;
    padding: 0;
}
.lqbui-nav-tree * {
