}

.lqbui-nav-item {
    line-height: 40px;
    display: block;
    width: 100%;
    position: relative;
    display: inline-block;
    vertical-align: middle;
    margin-bottom:1px; 

}

.lqbui-nav-tree .lqbui-nav-item a {
