        }else{
            this.getUserMenu();
        }
        window.addEventListener("popstate", function () {
            _this.locPath=(location.pathname+location.hash=="/")?"/entreprise.html":location.pathname+location.hash;
        })
    },
    methods:{
        setMenuData(data){
            let menu=[],temp={};
            for(let k=0;k<data.length;k++){
                data[k].fold=this.fold?this.fold:this.defalut;
               if(data[k].parentId==0||!temp[data[k].parentId]){
                   menu.push(data[k]);
                   temp[data[k].id]=data[k];
                   temp[data[k].id].child=[];
               }else{
                    temp[data[k].parentId].child.push(data[k]);
               }

            }
            return menu;
        },
        getUserMenu(){
            var _this=this;
            userInfo=utils.getItem("userInfo")&&JSON.parse(utils.getItem("userInfo"));
            if (userInfo) {
            auth_token = userInfo.auth_token;
            systemId = userInfo.systemId;
            ajax.get(fn.urlAuth + 'ams/api/user/getUserMenus/' + userInfo.userId + '/' + userInfo.systemId + '?auth_token=' + userInfo.auth_token, // 跳转到 action
                {},function(res) {
                    _this.$emit("isload");
                    if(!res){
                        console.log("异常！");
                        return;
                    }
                    if (res.code == 200) {
                        console.log('菜单目录：', res)
                        _this.menu=_this.setMenuData(res.data);
                        
                    } else {
                        if(res.code="A400600"){
                             location.href = fn.localUrl + "/ams/login.html"
                        }
                        try {
                            fn.commonWindowWarn(res.message)
                        } catch (error) {
                            alert(res.message);
                        }
                    }
                }
            )
            } else {
            //登录页面地址
            location.href = fn.localUrl + "/ams/login.html"
            }

