<template>
    <span class="lqb-form-compontent">
        <span v-if="data.label" class="lqb-comtitle">{{data.label}}：</span> 
        <span v-else class="lqb-comtitle"></span> 
        <DatePicker
            v-model="inputValue[data.model]" 
            :placeholder="data.placeholder"
            :type="data.type"
            align="cneter"
            range-separator="至"
            start-placeholder="开始日期"
            end-placeholder="结束日期"

            >
        </DatePicker>
    </span>
</template>

<script>
import {DatePicker} from "element-ui";

export default {
    data(){
        return{
            inputValue:this.value
        }
    },
    props:["value","size","data","space"],
    mounted(){

    },
    components:{
        DatePicker
    },
    computed:{
        space(){
            return (this.size=="long")?"480px":"165px"
        },
        inputValue:{
            get:function() {
                return this.value;
            },
            set:function(val) {
                this.$emit('input', val);
            }
        }
    }

}
</script>

<style>

</style>



