<template>
    <div  class="lqb-lineform lqb-afclear">
        <span style="display:inline-block"  :key="'lqb'+index" v-for="(item,index) in data" >
            <slot v-if="item.slotname" :name="item.slotname" ></slot>
            <component v-else style="min-width:260px" size="small" v-model="values" :data="item"  :is="item | setName"></component>
        </span>
    </div>
</template>

<script>
import lqbInviteCode from "../menber/InviteCode.vue";
import lqbInput from "../common/Input.vue";
import lqbSelect from "../common/Select.vue";
import lqbDatePicker from "../common/Data.vue";

import lqbUpload from "../common/Upload.vue";


export  default {
    data(){
        return{
            values:this.value?this.value:{}
        }
    },
    computed:{
        values:{
            get:function() {
                return this.value;
            },
            set:function(val) {
                this.$emit('input', val);
            }
        }
    },
    filters:{
        setName(data){
            if(data.slotname)
            {
                return "slot";
            }
            if(data.name){
                return "lqb"+data.name;
            }
            if(typeof data=="string"){
                return "lqb"+data;
            }
            return null;
        }
    },
    methods:{
        setName(data){
            if(data.slotname)
            {
                return data.slotname;
            }
            if(data.name){
                return "lqb"+data.slotname;
            }
            if(typeof data=="string"){
                return "lqb"+data;
            }
            return null;
        }
    },
    props:["data","value"],
    components:{
        lqbInviteCode,
        lqbInput,
        lqbSelect,
        lqbDatePicker,
        lqbUpload
    },
    mounted(){
        
    }
}
</script>

<style>

    
    .lqb-lineform .lqb-form-compontent{
        color: #000000;
        float: left;
            padding: 5px;
    }
    
    </style>



