<style scoped>
.lqbui-main-header *{list-style: none;font-size: 14px;}
.lqbui-main-header{
  background-color: #3c8dbc;
  height: 50px;
  box-shadow: 12px 3px 5px rgba(1,1,1,0.2);
  position: fixed;
  width: 100%;
  z-index: 100;
  top: 0px;
}

.lqbui-logo{
  display: block;
    float: left;
    padding: 7px 20px;
    background-color: #367fa9;
}
.lqbui-logo .lqbui-logo-lg{

}
.lqbui-logo .lqbui-logo-lg img{
    vertical-align: middle;
}
.lqbui-navbar-custom-menu{
  float: right;
}
.lqbui-user-menu{
       
}
.lqbui-nav li{
    float: right;
}
.lqbui-user-menu a{
 color: #ffffff;
        line-height: 20px;
        cursor: pointer;
         padding:15px;
         display: block;
}
.lqbui-user-menu a:hover{
  background: rgba(0,0,0,0.1);
}
.lqbui-user-image{
     float: left;
    width: 25px;
    height: 25px;
    border-radius: 50%;
    margin-right: 10px;
    margin-top: -2px;
}
</style>
<template>
  <header class="lqbui-main-header">
            <a href="/" class="lqbui-logo">
                <span class="lqbui-logo-lg">
                    <img src="data:image/png;base64,">
                </span>
            </a>
            <div class="lqbui-navbar-custom-menu">
                  <ul class="lqbui-nav lqbui-navbar-nav">
                      <li class="lqbui-user-menu">
                          <a @click="loginout" >
                              <img src="data:image/png;base64," class="lqbui-user-image" alt="User Image">
                              <span class="hidden-xs">登出</span>
                          </a>
                      </li>
                      <li v-if="enterpriseList" style="line-height: 50px;">
                            <select class="commonInput" v-model="id" @change="setEnterpriseId">
                                <option  :key="index"  v-for="(item, index) in enterpriseList" :value="item.id ">{{item.enterpriseName}}</option>
                            </select>
                        </li>
                  </ul>
              </div>
           
    </header>
</template>

<script>
import utils from "../../lib/utils.js";
import fn from "../../lib/fn.js";
export default {
    data(){
        return{

        }
    },
  computed:{
      id:function(){
         return this.faultCom;
      }
  },
  props:["enterpriseList","faultCom"],
  methods:{
    loginout(e){
        e.preventDefault();
        e.stopPropagation();
        utils.removeItem("userInfo");
        //登录页面地址
        location.href = fn.localUrl + "/ams/login.html"
    },
    setEnterpriseId(e){
        this.$emit("changecompont",e.target.value);
    }
  },
  mounted(){
      
  }
}
</script>





