<template>
    <span class="lqb-form-compontent">

        <span v-if="data.label" class="lqb-comtitle">{{data.label}}：</span> 
        <span v-else class="lqb-comtitle"></span> 
        <lqbInput :style="'width:'+space"  v-model="inputValue[data.model]" :placeholder="data.placeholder" ></lqbInput>
    </span>
</template>

<script>
import {Input} from "element-ui";

export default {
    data(){
        return{
            inputValue:this.value
        }
    },
    props:["data","value","size"],
    mounted(){
    },
    components:{
        "lqbInput":Input
    },
    computed:{
        space(){
            return (this.size=="long")?"480px":"165px"
        },
        inputValue:{
            get:function() {
                return this.value;
            },
            set:function(val) {
                this.$emit('input', val);
            }
        }
    }
}
</script>

<style>

</style>



