<template>
    <div class="lqb-form">
        <component  v-model="values" :value="values"   :data="data?data:[]" :is="'lqb'+(fromType?fromType:'LineForm')">
            <span class="lqb-slot-warp" v-if="index!='default'" :slot="index" :key="index" v-for="(item,index) in $slots" style="display:inline-block">
                <slot :name="index"></slot>
            </span>
        </component>
        <slot></slot>
    </div>
</template>

<script>
import lqbLineForm  from "./LineForm.vue";
import lqbRowForm  from "./RowForm.vue";
import lqbListForm  from "./ListForm.vue";

export  default {
    data(){
        return{
            values:this.value?this.value:{}
        }
    },
    computed:{
        values:{
            get:function() {
                return this.value;
            },
            set:function(val) {
                this.$emit('input', val);
            }
        }
    },
    props:["data","value","fromType"],
    components:{
        lqbLineForm,
        lqbRowForm,
        lqbListForm
    },
    mounted(){
        console.log(this);
    }
}
</script>

<style>

    .lqb-form{
        padding: 5px;
            
    }
    
    </style>



