<template>
    <span class="lqb-form-compontent">
        <span style="vertical-align: top;" v-if="data.label" class="lqb-comtitle">{{data.label}}：</span> 
        <span v-else class="lqb-comtitle"></span> 
             <div :key="item" v-for="item in data.filelen" class="lqb-common-file lqb-society_img">
                <div class="lqb-common-imgbox " :style="'width:'+data.opt[item-1].width" >
                    <i class="lqb-upload-icon icon iconfont icon-shangchuantupian "></i>
                    <img v-if="inputValue[data.model][item-1]" :src="inputValue[data.model][item-1].preview?inputValue[data.model][item-1].preview:inputValue[data.model][item-1].src" alt="">
                    <input @change="uploadimg" :data-key="item" type="file"  name="file" class="uploading">
                </div>
                <p style="padding:5px">
                    {{data.opt[item-1].msg}}
                </p>
            </div>
    </span>
</template>

<script>
//import {Input,Upload} from "element-ui";
import utils from "../../lib/utils.js";
import fn from "../../lib/fn.js";
let ajax=require("ajax");
window.userInfo = window.userInfo||{}, //userId, systemId, auth_token, sso_token
        window.auth_token = "", 
        window.systemId = "";
export default {
    data(){
        return{
            inputValue:this.value,
            loads:false
        }
    },
    props:["data","value","size"],
    mounted(){
    },
    components:{
        //"lqbInput":Input,
        //Upload
        
    },
    computed:{
        space(){
            return (this.size=="long")?"480px":"165px"
        },
        inputValue:{
            get:function() {
                return this.value;
            },
            set:function(val) {
                this.$emit('input', val);
            }
        }

    },
    methods:{
        beforeUpload(e){
            return false;
        },
        uploadimg(e){
            console.log(e.target.getAttribute("data-key"));
            this.genSignature(e.target);
        },
        //图片上传
        genSignature: function(obj) {
            var file = obj.files[0];
            let _this=this;

            let indx=obj.getAttribute("data-key")

            this.checkFileSize(file,function(imgageSource){
                            
                if(window.location&&(window.location.href.indexOf("file://")>0||window.location.href.indexOf("localhost")>0)){
                    if(!_this.inputValue[_this.data.model]){
                        window.Vue.set(_this.inputValue,_this.data.model,[]);
                    }
                    if(!_this.inputValue[_this.data.model][indx-1])
                    {
                        window.Vue.set(_this.inputValue[_this.data.model],indx-1,{});
                    }
                    _this.inputValue[_this.data.model][indx-1].preview=imgageSource;
                    _this.inputValue[_this.data.model][indx-1].src=imgageSource;
                }
                             
                ajax.get(fn.userUrl + "/upload/photo/getSignature?auth_token=" + userInfo.auth_token,{},
                         function(result) {
                            console.log('结果：', result)
                            if (result.code == 200 && result.successFlag) {
                                var data = result.data;
                                var formData = new FormData();
                                var url = data.host;
                                var fileName = file.name;
                                var arrFile = fileName.split('.');

                                /*数据顺序oss有强要求，不要轻易更改*/
                                formData.append('key', data.dir + fileName.replace(/(.*)\./, data.filename + '.'));
                                formData.append('policy', data.policy);
                                formData.append('OSSAccessKeyId', data.accessid);
                                formData.append('signature', data.signature);
                                formData.append('success_action_status', '200');
                                formData.append('callback', data.callback);
                                formData.append('x:businesstype', data.businessType);
                                formData.append('x:dir', data.dir);
                                formData.append('x:originalfilename', fileName);
                                formData.append('file', file);
                                var imgStr = data.host + '/' + data.dir + data.filename + '.' + arrFile[arrFile.length - 1]
                                console.log('图片地址：', data.host + '/' + data.dir + data.filename + '.' + arrFile[arrFile.length - 1])
                                uploadToOss(url, formData);
                             if(!_this.inputValue[_this.data.model]){
                                window.Vue.set(_this.inputValue,_this.data.model,[]);
                              }
                              if(!_this.inputValue[_this.data.model][indx-1])
                              {
                                window.Vue.set(_this.inputValue[_this.data.model],indx-1,{});
                              }
                              _this.inputValue[_this.data.model][indx-1].preview=imgageSource;
                              _this.inputValue[_this.data.model][indx-1].src=imgStr;
                                function uploadToOss(url, data) {
                                    ajax.post(url,data,function (data) {
                                            console.log('test', data);
                                            if (data && data.success) {

                                            }
                                        }
                                    );
                                }
                            }else
                            {

                            }
                    });
            },function(e){
                fn.commonWindow(e);
            })
            
        },
        checkFileSize(file,success,fail){
            if(!(file.type == "image/jpeg" || file.type == "image/png" || file.type == "image/jpg")){
                fail("图片格式不正确！");
                return false;
            }
            var oFReader = new FileReader();
            oFReader.readAsDataURL(file);
             oFReader.onload = function (oFREvent) {
                 var imgs=new Image();
                 imgs.onload=function(){
                    // console.log(imgs.height,imgs.width,oFREvent)
                       success(oFREvent.target.result);
                 }
                 imgs.onerror=function(){
                    fail("图片读取失败");
                  }
                 oFREvent.target.result.width;
                 imgs.src=oFREvent.target.result;
             };
             oFReader.onerror=function(e){
                fail("图片读取失败");
             }
        }
        
    }

}
</script>

<style>
.lqb-upload-icon{
    position: absolute;
    top: 50%;
    left: 50%;
    margin-left: -23px;
    margin-top:-23px; 
    font-size: 46px !important;
}
.lqb-common-file{

    display: inline-block;
    margin: 0 15px 0 0;
}
.lqb-common-imgbox{
    height: 200px;
    position: relative;
    background-size: cover;
    border:1px dashed #adadad;
    border-radius: 5px;
    
}
.lqb-common-imgbox img {
    position: absolute;
    width: 100%;
    height: 100%;
    left: 0;
}
.lqb-common-imgbox input {
    width: 100%;
    height: 100%;
    cursor: pointer;
    opacity: 0;
}
</style>



