<template>
    <div  class="lqb-rowform lqb-afclear">
        <el-row :key="'lqb'+index" v-for="(item,index) in data">
            <slot v-if="item.slotname" :name="item.slotname" ></slot>
            <component v-else size="long"  v-model="values"   :data="item" :name="item.slotname" :is="item | setName"></component>
        </el-row>
    </div>
</template>

<script>
import lqbInviteCode from "../menber/InviteCode.vue";
import lqbInput from "../common/Input.vue";
import lqbSelect from "../common/Select.vue";
import lqbDatePicker from "../common/Data.vue";
import lqbUpload from "../common/Upload.vue";
export  default {
    data(){
        return{
            values:this.value?this.value:{}
        }
    },
    computed:{
        values:{
            get:function() {
                return this.value;
            },
            set:function(val) {
                this.$emit('input', val);
            }
        }
    },
    props:["data","value"],
    filters:{
        setName(data){
            if(data.slotname)
            {
                return "slot";
            }
            if(data.name){
                return "lqb"+data.name;
            }
            if(typeof data=="string"){
                return "lqb"+data;
            }
            return null;
        }
    },
    components:{
        lqbInviteCode,
       // lqbRow:Row,
        lqbInput,
        lqbSelect,
        lqbDatePicker,
        lqbUpload
    },
    mounted(){
        console.log(this.values.Tel);
    }
}
</script>

<style>
.lqb-rowform{
  
}
.lqb-rowform .lqb-form-compontent{
   display: block;
   padding: 5px;
}
.lqb-rowform  .lqb-comtitle{
    display: inline-block;
    width: 126px;
    
    }
  .lqb-rowform  .lqb-slot-warp{
      display: block !important;
  }
    </style>



