<template>
    <span class="lqb-form-compontent">
        <span v-if="data.label" class="lqb-comtitle">{{data.label}}：</span> 
        <span v-else class="lqb-comtitle"></span> 
        <lqbSelect @change="data.changes" :style="'width:'+space"  v-model="inputValue[data.model]" :placeholder="data.placeholder" >
            <lqbOption
                v-for="item in inputValue[data.model+'Option']"
                :key="item.value"
                :label="item.label"
                :value="item.value">
            </lqbOption>    
        </lqbSelect>
    </span>
</template>
<script>
import {Select,Option} from "element-ui";
export default {
    data(){
        return{
            inputValue:this.value
        }
    },
    props:["data","value","size"],
    mounted(){
    },
    components:{
        "lqbSelect":Select,
        "lqbOption":Option
    },
    methods:{
        changes(e){
        }
    },
    computed:{
        space(){
            return (this.size=="long")?"480px":"165px"
        },
        inputValue:{
            get:function() {
                return this.value;
            },
            set:function(val) {
                this.$emit('input', val);
            }
        }
    }
}
</script>

<style>

</style>



